___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: Arial, sans-serif;
    background: #f9f9f9;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .container {
    flex: 1;
    padding: 20px;
    max-width: 900px;
    margin: 0 auto;
  }
  
  h1, h2 {
    margin-bottom: 10px;
  }
  
  ul {
    list-style: none;
    padding-left: 0;
    margin-bottom: 15px;
  }
  
  li {
    padding: 10px;
    background: #eee;
    margin-bottom: 8px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  li.completed {
    text-decoration: line-through;
    color: gray;
  }
  
  .btn {
    padding: 6px 12px;
    border: none;
    background: #0077ff;
    color: white;
    cursor: pointer;
    border-radius: 4px;
    transition: 0.3s;
  }
  
  .btn:hover {
    background: #005fcc;
  }
  
  footer {
    text-align: center;
    padding: 10px;
    background: #f1f1f1;
    font-size: 0.9rem;
  }
  
  footer a {
    color: inherit;
    text-decoration: none;
  }
  
  footer a:hover {
    text-decoration: underline;
  }
  
  /* Popup form */
  .popup {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    justify-content: center;
    align-items: center;
  }
  
  .popup form {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    width: 300px;
    display: flex;
    flex-direction: column;
  }
  
  .popup form label {
    margin-top: 10px;
  }
  
  .popup-buttons {
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
  }
  
  .cancel-btn {
    background: #ccc;
    color: #333;
  }
  
  .cancel-btn:hover {
    background: #999;
  }
